import vue, {lib, library} from "vue.min.js"
var router = []
router.push ({path: "/", name: "home:page", component: () => import ("./index.vue"), meta: {}})
router.push ({path: "/about", name: "page:about", component: () => import ("./page/about.vue"), meta: {}})
export default router = lib.router.create ({history: lib.router.history (vue.request.url), [("route").concat ("s")]: router})