<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "vue",
	properties: [],
	setup: function () {
		vue.start ()
		return vue.export ({the, layout: vue.compute (vue.layout)}, function setup () {})
		},
	mount: function () {
		vue.emit ("app:mount")
		},
	eject: function () {
		if (vue.date.cache) lib.time.interval.clear (vue.date.cache)
		if (vue.ping) if (vue.ping.cache) lib.time.interval.clear (vue.ping.cache)
		vue.emit ("app:eject")
		},
	create: function () {
		vue.emit ("app:create")
		},
	update: function () {
		vue.parse_url ()
		vue.emit ("ch:url")
		vue.emit ("app:update")
		},
	method: {
		slot () { return vue.slot (this).default },
		},
	})
function the () {}
</script>

<template>
	<media-query:screen screen/>
	<media-query:line line/>
	<media-query:audio audio/>
	<service:notification v-bind:data="vue.notification ()"/>
	<service:toast v-bind:data="vue.toast ()"/>
	<component v-bind:is="layout">
		<router:view id="router:view"/>
	</component>
</template>

