<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "media-query:line",
	properties: [],
	setup: function () {
		var line = vue.reference ("on")
		vue.watch (vue.on.line, function (status) {
			if (status) line.value = "on"
			else line.value = "off"
			})
		return {vue, line}
		},
	})
</script>

<template name="media-query:line">
	<div v-if="line === 'off'" v-bind:value="line" id="media-query:line">
		<div class="media-query:line">
			off:line
		</div>
	</div>
</template>

<style lang="less">
@import "/src/style.less";
@import "/src/style/sheet.less";
[id="media-query:line"] {
	.fix; .top; .left;
	.wide;
	background-color: @white-alpha;
	}
[id="media-query:line"][value="on"] {
	display: none;
	}
[id="media-query:line"][value="off"] {
	.in-dex;
	padding: @wide-extra;
	}
[id="media-query:line"][value="off"] > [class="media-query:line"] {
	height: @wide;
	background: @white;
	border: @border-solid #aaaaaa;
	border-radius: @border-radius-sky;
	box-shadow: @box-shadow-s @gray;
	}
</style>
