import vue, {lib, library} from "vue.min.js"

vue.populate = function (state) { if (state in vue.populate.state) vue.populate.state [state].call () }
vue.populate.state = {
	dummy: function () {
		vue.db.select ("dummy").then (function (db) {
			if (db.data) {
				vue.x.commit ("dummy", db.data)
				}
			})
		},
	}

export default vue.store ({
	strict: true,
	state: {},
	mutation: {
		mutation (state, context) { context.call (null, state) },
		dummy (state, data) { state.dummy = data },
		},
	action: {},
	})
